import React from "react";
import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import { AuthProvider } from "./contexts/AuthContext";
import ProtectedRoute from "./components/ProtectedRoute";
import Navbar from "./components/Navbar";
import Home from "./pages/Home";
import Login from "./pages/Login";
import Register from "./pages/Register";
import StoreRegister from "./pages/StoreRegister";
import DeliveryRegister from "./pages/DeliveryRegister";
import AdminRegister from "./pages/AdminRegister";
import Profile from "./pages/Profile";
import EmailVerificationSuccess from "./pages/EmailVerificationSuccess";
import TestEmailVerification from "./pages/TestEmailVerification";
import NavigationTest from "./pages/NavigationTest";
import ImageGallery from "./pages/ImageGallery";
import AuthTest from "./pages/AuthTest";
import AdminDashboard from "./pages/AdminDashboard";
import Test from "./pages/Test";

function App() {
  return (
    <AuthProvider>
      <Router>
        <div className="min-h-screen flex flex-col">
          {/* Navigation */}
          <Navbar />

          {/* Main Content */}
          <main className="flex-grow">
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/login" element={<Login />} />
              <Route path="/register" element={<Register />} />
              <Route path="/register/store" element={<StoreRegister />} />
              <Route path="/register/delivery" element={<DeliveryRegister />} />
              <Route path="/register/admin" element={<AdminRegister />} />
              <Route path="/email-verification-success" element={<EmailVerificationSuccess />} />
              <Route path="/test-email" element={<TestEmailVerification />} />
              <Route path="/nav-test" element={<NavigationTest />} />
              <Route path="/auth-test" element={<AuthTest />} />
              <Route path="/images" element={<ImageGallery />} />
              <Route 
                path="/profile" 
                element={
                  <ProtectedRoute>
                    <Profile />
                  </ProtectedRoute>
                } 
              />
              <Route 
                path="/admin" 
                element={
                  <ProtectedRoute requireAdmin={true}>
                    <AdminDashboard />
                  </ProtectedRoute>
                } 
              />
              <Route path="/test" element={<Test />} />
              {/* Add a catch-all route for 404 pages */}
              <Route path="*" element={<Navigate to="/" />} />
            </Routes>
          </main>

          {/* Footer */}
          <footer className="bg-gray-100 p-4 text-center text-gray-600">
             {new Date().getFullYear()} FreshCart. All rights reserved.
          </footer>
        </div>
      </Router>
    </AuthProvider>
  );
}

export default App;
